<template>
  <div class="playerContainer"
       @mousemove="onMouseMove">

    <!-- 大图/白板 -->
    <div class="areaUpTools">
      <PanoWhiteboard v-show="isWhiteboardOpen" />
      <!-- <MainView v-if="!isWhiteboardOpen" /> -->
    </div>

    <!-- MenuBar -->
    <!-- <div :class="{
        toolWrapper: true,
        toolWrapperHidden: !(showToolbar || isMouseOnToolbar)
      }"
         @mouseEnter="onMouseEnterMenu"
         @mouseLeave="onMouseLeaveMenu">
      <div class="toolBar">
        <Button class="btnClass"
                @click="onClickMicMute">
          <AudioLevel :userId="userMe.userId"
                      :audioMuted="userMe.audioMuted"
                      :fontSize="22" />
          <span style="margin-left: 0px">
            {{ userMe.audioMuted ? '取消静音' : '静音' }}
          </span>
        </Button>

        <Button class="btnClass"
                @click="onClickCamMute">
          <i v-if="userMe.videoMuted"
             class="iconfont icon-video-camera1"
             style="color: red" />
          <i v-else
             class="iconfont icon-video-camera1" />
          <span style="margin-left: 0px">
            {{ userMe.videoMuted ? '打开视频' : '关闭视频' }}
          </span>
        </Button>
        <Button :class="{
            btnClass: true,
            btnClass__contentUpdated: !isWhiteboardOpen && whiteboardUpdated
          }"
                @click="onClickWhiteboard">
          <i class="iconfont icon-pencil-square-o" />
          <span style="margin-left: 0px">
            {{ isWhiteboardOpen ? '关闭白板' : '打开白板' }}
          </span>
        </Button>
        <Button :class="{
            btnClass: true,
            btnClass__active: userMe.screenOpen
          }"
                @click="onClickScreen">
          <i class="iconfont icon-external-link" />
          <span style="margin-left: 0px">
            {{ userMe.screenOpen ? '正在共享' : '共享屏幕' }}
          </span>
        </Button>
        <Button class="btnClass" @click="onClickInvite">
          <i class="iconfont icon-user-plus" />
          <span style="margin-left: 0px">邀请</span>
        </Button>
        <Button class="btnClass"
                @click="openSetting">
          <i class="iconfont icon-cog" />
          <span style="margin-left: 0px">设置</span>
        </Button>

        <Button class="btnClass"
                @click="onExit">
          <i class="iconfont icon-exit" />
          <span style="margin-left: 0px">退出</span>
        </Button>
      </div>
    </div> -->
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';
import /* { RtsService } */PanoRtc, { statusCodeToQResult, RtsService } from '@pano.video/panortc';
// import MainView from '../components/userVideo/MainView';
// import SmallVideoList from '../components/SmallVideoList';
// import MeetingInfo from '../components/MeetingInfo';
// import AudioLevel from '@/components/AudioLevel';
import PanoWhiteboard from '@/components/whiteboard/PanoWhiteboard';
import { applyForWbAdmin } from '@/utils/panorts';
import {
  enableOpenVideo,
  enableOpenAudio,
  updateCaptureDeviceId
} from '@/utils/panortc';
import * as Constants from '@/constants';
import { v4 as uuidv4 } from 'uuid';
import { getToken } from '@/api/whiteBoard'
export default {
  name: 'Meeting',
  data() {
    return {
      showToolbar: false,
      isMouseOnToolbar: false,
      hideToolbarTimer: '',
      closeConfirmVisible: false
    };
  },
  components: {
    PanoWhiteboard,
    // SmallVideoList,
    // MainView,
    // MeetingInfo,
    // AudioLevel
  },
  computed: {
    ...mapGetters([
      'isWhiteboardOpen',
      'userMe',
      'whiteboardUpdated',
      'wbAdminUser',
      'meetingStatus',
      'meetingEndReason',
      'videoPorfile',
      'whiteboardAvailable'
    ])
  },
  watch: {
    videoPorfile() {
      if (!this.userMe.videoMuted) {
        const r = window.rtcEngine.startVideo(this.videoPorfile);
        console.log('startVideo with new profile type', this.videoPorfile, r);
      }
    },
    meetingStatus() {
      switch (this.meetingStatus) {
        case 'ended':
          this.$alert(`您已离开会议，因为${this.meetingEndReason}`, '提示')
            .then(this.leaveChannel)
            .catch(this.leaveChannel);
          break;
        case 'countdownover':
          this.$alert('会议时间已用完，会议已结束', '提示')
            .then(this.leaveChannel)
            .catch(this.leaveChannel);
          break;
        case 'reconnecting':
          this.$message.info('已从服务器断开，正在重连...');
          break;
        case 'disconnected':
          this.$confirm(
            '无法连接到服务器，已离开会议，请尝试重新加入会议',
            '提示'
          )
            .then(this.leaveChannel)
            .catch(this.leaveChannel);
          break;
        case 'connected':
          this.$message.success('会议重连成功');
          break;
        default:
          break;
      }
    }
  },
  methods: {
    ...mapMutations([
      'updateUser',
      'setWhiteboardOpenState',
      'resetUserStore',
      'resetMeetingStore',
      'setSettingVisible',
      'setAppId',
      'setPanoToken',
      'setChannelId',
      'updateUserMe',
      'setMeetingStatus',
    ]),
    // 打开白板
    openWhiteBoard() {
      const { channelId, userName, appId, userId, token } = this.form;
      window.rtcWhiteboard.joinChannel(
        {
          appId,
          token,
          channelId,
          name: userName,
          userId: userId
        },
        () => {
          console.log('panorts whiteboard join seccess!');
          this.onJoinChannelSuccess();
          this.onClickWhiteboard()
        },
        statusCode => {
          window.rtcEngine.leaveChannel();
          console.log('panorts whiteboard join failed', statusCode);
          this.onJoinChannelFailed(
            `PanoRts服务连接失败: ${statusCodeToQResult(statusCode)}`
          );
        }
      );
    },
    // 获取token
    getToken(channelId, userId) {
      return new Promise((resolve, reject) => {
        getToken({ channelId, userId }).then((res) => {
          console.log(res);
          if (res.data.code == 0) {
            resolve(res.data.content.token)
          }
        }).catch(error => {
          console.error(error)
        })
      })
    },
    /**
    * 加会逻辑
    */
    async joinChannel() {
      // const { channelId, userName, appId, userId, token } = this.form;
      // 房间号
      let channelId = '12345678',
        userName = uuidv4().split('-')[0], // 用户名
        appId = 'a632a1053992479a9ac5f673fb96c713',
        // appId = 'f8505b9462494b5ba4ce403e1c2b8271',
        userId = parseInt(Math.random() * 100000) + ''
      let isMobile = await this.isMobile()
      let token = ''
      // token = await this.getToken(channelId, userId)
      if (isMobile) {
        this.$message('用的token1')
        token = '01000003RThyRHhYSWaMbqHJFxZGIjSEfU3v8LliX/Y3o+Y2A6iGkJeLP5BR7ruUUSstt1wFt7cY/xwvtu9gZVgWbkfh7MnSjSS6EAYz4EcMXmy7cb0lCWl3BflrulK7NnOOSHqHPn4A73CFUIYNtcDpgH2t34iHyb4/1shjib6cNUlK//JAH32ajqH++C3Hva+mblt9XOXejq07WDXgo7Go5QwzGWEoXr3OfEtk/J7zmC3DvPjQxd/j02VD/HIY7sQSNUShUcta3K11nqhtFAgSp4W/ZbjfenMiF2Ej'
      } else {
        this.$message('用的token2')
        token = '01000003U01yTWRUS0tb3u3tuElX5920SyoGhgSCehw+WlU2KUOM37lCmJfvGF/1BG0hlPCKgI5JDnEW4NEj1GIQGPTJeLUdW9MVtuqZznmyUVeBI2raG9Z0tKTGj4ArArw+uAWY9J5WULvzQbrIefaDaNEV59aWUbiUVgyQ+0/XaSh2q+GRlIhFMokIQVPlhFNKYaK9uATj7ymVEgQrV9+TFg0qjHtUVk0v7PBzeGTOu4gWJEyR2j9AGTTvibkcMMRS2YUOlx/WNhQO+/E6gd19zx6CRZny9M6EZFNa'
      }
      console.log(token);

      this.form = { channelId, userName, appId, userId, token };
      if (!channelId || !appId || !userId || !token) {
        this.$message.warning('请填写必要的参数再加入会议');
        return;
      }
      localStorage.setItem(Constants.localCacheKeyAppId, appId);
      localStorage.setItem(Constants.localCacheKeyToken, token);
      localStorage.setItem(Constants.localCacheKeyChannelId, channelId);
      localStorage.setItem(Constants.localCacheKeyUserName, userName);
      localStorage.setItem(Constants.localCacheKeyUserId, userId);
      this.setAppId(appId);
      this.setPanoToken(token);
      this.setChannelId(channelId);
      this.updateUserMe({
        userName,
        userId
      });
      console.log('joinChannel...');
      const qResult = window.rtcEngine.joinChannel(
        {
          appId,
          token,
          channelId,
          channelMode: Constants.ChannelMode.Mode_Meeting,
          userId,
          userName
        },
        {
          joinChannelType: PanoRtc.Constants.JoinChannelType.mediaOnly
        }
      );
      console.log(qResult);
      if (qResult.code != 'OK') {
        console.error('call joinChannel API error:', qResult);
        this.$message.error(
          'call joinChannel API error, please check console message.'
        );
      }
      this.resetMeetingStore();
      setTimeout(() => {
        this.openWhiteBoard()
      }, 500)
    },
    /**
    * 入会成功回调
    */
    onJoinChannelConfirm(data) {
      if (data.result === 'success') {
        console.log('rtcEngine 入会成功');
        // 媒体入会成功之后再连接白板，因为用户列表从媒体连接中获取
        // 而标注等事件从白板（rts）中获取，保证在接收到用户开启标注事件时，用户已经存在于用户列表中
        const { channelId, userName, appId, userId, token } = this.form;
        window.rtcWhiteboard.joinChannel(
          {
            appId,
            token,
            channelId,
            name: userName,
            userId: userId
          },
          () => {
            console.log('panorts whiteboard join seccess!');
            this.onJoinChannelSuccess();
          },
          statusCode => {
            window.rtcEngine.leaveChannel();
            console.log('panorts whiteboard join failed', statusCode);
            this.onJoinChannelFailed(
              `PanoRts服务连接失败: ${statusCodeToQResult(statusCode)}`
            );
          }
        );
      } else {
        this.onJoinChannelFailed(`PanoRtc服务连接失败：${data.message}`);
      }
    },
    onJoinChannelSuccess() {
      this.loading = false;
      const { audioOn, videoOn } = this.form;
      localStorage.setItem(
        Constants.localCacheKeyMuteMicAtStart,
        audioOn ? 'no' : 'yes'
      );
      localStorage.setItem(
        Constants.localCacheKeyMuteCamAtStart,
        videoOn ? 'no' : 'yes'
      );
      this.setMeetingStatus('connected');
      // this.$router.replace({ name: 'Meeting' });
    },
    onJoinChannelFailed(reason) {
      this.loading = false;
      this.$message.error(reason);
    },
    onMouseMove() {
      this.showToolbar = true;
      clearTimeout(this.hideToolbarTimer);
      this.hideToolbarTimer = setTimeout(() => {
        this.showToolbar = false;
      }, 2000);
    },
    onMouseEnterMenu() {
      this.isMouseOnToolbar = true;
    },
    onMouseLeaveMenu() {
      this.isMouseOnToolbar = false;
    },
    async checkEnableOpenAudio() {
      const enable = await enableOpenAudio();
      if (!enable) {
        this.$message.info('没有检测到麦克风');
        return false;
      }
      return true;
    },
    async onClickMicMute() {
      if (!(await this.checkEnableOpenAudio())) {
        return;
      }
      if (this.userMe.audioMuted) {
        window.rtcEngine.unmuteMic();
      } else {
        window.rtcEngine.muteMic();
      }
      this.updateUser({
        userId: this.userMe.userId,
        audioMuted: !this.userMe.audioMuted
      });
    },
    async onClickCamMute() {
      const enable = await enableOpenVideo();
      if (!enable) {
        this.$message.info('没有检测到摄像头');
        return;
      }
      if (this.userMe.videoMuted) {
        await updateCaptureDeviceId();
        window.rtcEngine.startVideo(this.videoPorfile);
      } else {
        if (this.userMe.videoAnnotationOpen) {
          // 关闭视频标注
          RtsService.getInstance()
            .getAnnotation(this.userMe.userId, 'video')
            ?.stop();
        }
        window.rtcEngine.stopVideo();
      }
      this.updateUser({
        userId: this.userMe.userId,
        videoMuted: !this.userMe.videoMuted
      });
    },
    onClickWhiteboard() {
      if (!this.whiteboardAvailable) {
        this.$message.info('白板尚未准备好，请稍后重试');
        return;
      }
      this.setWhiteboardOpenState(!this.isWhiteboardOpen);
      // 如果没有演示用户，且是我打开了白板，那么广播我是演示用户
      if (this.isWhiteboardOpen && !this.wbAdminUser) {
        applyForWbAdmin();
      }
    },
    onClickScreen() {
      if (!this.userMe.screenOpen) {
        const result = window.rtcEngine.startScreen();
        // eslint-disable-next-line prettier/prettier
        if (result && result.code === 'NOT_SUPPORTED') {
          this.$message.info(
            'Safari浏览器从版本13开始支持屏幕共享，当前浏览器不支持'
          );
          window.rtcEngine.stopScreen();
        } else {
          this.updateUser({
            userId: this.userMe.userId,
            screenOpen: !this.userMe.screenOpen
          });
        }
      } else {
        window.rtcEngine.stopScreen();
        this.updateUser({
          userId: this.userMe.userId,
          screenOpen: !this.userMe.screenOpen
        });
      }
    },
    openSetting() {
      this.setSettingVisible(true);
    },
    closeSetting() {
      this.setSettingVisible(false);
    },
    onExit() {
      this.$msgbox
        .confirm('确认退出会议', '提示')
        .then(this.leaveChannel)
        .catch(() => console.log('取消退出'));
    },
    leaveChannel(goLogin = true) {
      window.rtcEngine.leaveChannel();
      window.rtcWhiteboard && window.rtcWhiteboard.leaveChannel();
      if (this.userMe.videoAnnotationOpen) {
        // 关闭视频标注
        RtsService.getInstance()
          .getAnnotation(this.userMe.userId, 'video')
          ?.stop();
      }
      this.resetMeetingStore();
      this.resetUserStore();
      window.onbeforeunload = null;
      goLogin && this.$router.replace({ name: 'Login' });
    },
    isMobile() {
      return new Promise((resolve, reject) => {
        var sUserAgent = navigator.userAgent.toLowerCase();
        var bIsIpad = sUserAgent.match(/ipad/i) == "ipad";
        var bIsIphoneOs = sUserAgent.match(/iphone os/i) == "iphone os";
        var bIsMidp = sUserAgent.match(/midp/i) == "midp";
        var bIsUc7 = sUserAgent.match(/rv:1.2.3.4/i) == "rv:1.2.3.4";
        var bIsUc = sUserAgent.match(/ucweb/i) == "ucweb";
        var bIsAndroid = sUserAgent.match(/android/i) == "android";
        var bIsCE = sUserAgent.match(/windows ce/i) == "windows ce";
        var bIsWM = sUserAgent.match(/windows mobile/i) == "windows mobile";
        if (bIsIpad || bIsIphoneOs || bIsMidp || bIsUc7 || bIsUc || bIsAndroid || bIsCE || bIsWM) {
          resolve(true)
        } else {
          resolve(false)
        }

      })
    },
    // 获取url参数
    // getQueryString(name) {
    //   let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
    //   let r = this.props.location.search.substr(1).match(reg);
    //   if (r != null) {
    //     return unescape(r[2]);
    //   }
    //   return null;
    // },

    getQueryString(name) {
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      var r = window.location.search.substr(1).match(reg);//search,查询？后面的参数，并匹配正则
      if (r != null) return unescape(r[2]); return null;
    }

  },
  async mounted() {
    // if (this.meetingStatus !== 'connected') {
    //   this.$router.replace({ name: 'Login' });
    //   return;
    // }
    this.joinChannel();

    window.onbeforeunload = e => {
      const evt = e || window.event;
      if (evt) {
        evt.returnValue = '确定离开？';
      }
      return '确定离开？';
    };
    // const autoMuteAudio =
    //   localStorage.getItem(Constants.localCacheKeyMuteMicAtStart) == 'yes';
    // const autoOpenVideo =
    //   localStorage.getItem(Constants.localCacheKeyMuteCamAtStart) == 'no';

    // console.log('autoOpenVideo', autoOpenVideo, 'autoMuteAudio', autoMuteAudio);
    // if (autoOpenVideo) {
    //   this.onClickCamMute();
    // }
    // if (await this.checkEnableOpenAudio()) {
    //   window.rtcEngine.startAudio();
    //   this.updateUser({
    //     userId: this.userMe.userId,
    //     audioMuted: false
    //   });
    //   if (autoMuteAudio) {
    //     this.onClickMicMute();
    //   }
    // }
  },
  beforeDestroy() {
    this.leaveChannel(false);
    window.onbeforeunload = null;
  }
};
</script>
<style lang="scss" scoped>
$playerLeftOffset: 0;
$playerWidth: 100%;

.playerContainer {
  position: relative;
  width: $playerWidth;
  left: $playerLeftOffset;
  top: 0px;
  height: 100vh;
  background-color: #222;
  display: flex;
  flex-direction: column;
}

.areaUpTools {
  width: 100%;
  flex: 1;
  overflow: hidden;
}

.toolWrapper {
  position: absolute;
  z-index: 30;
  width: 100%;
  // height:60px;
  bottom: 0px;
  text-align: center;
  transition: all 0.5s ease-in-out;
  opacity: 1;
}

.toolWrapperHidden {
  bottom: -30px;
  opacity: 0;
}

.toolBar {
  position: absolute;
  bottom: 0px;
  left: 50%;
  transform: translate(-50%);
  // position: absolute;
  display: inline-block;
  // z-index: 2;
  height: 60px;
  padding: 0 20px;
  border-radius: 5px 5px 0 0;
  // left: $playerLeftOffset;
  // display: block;
  // bottom: 0px;
  // position: relative;
  // bottom:0;
  background-color: rgba(82, 83, 85, 0.6);
  color: #aaaaaa;
  text-align: center;
  display: flex;
}
.btnClass:hover,
.btnClass:active,
.btnClass:focus {
  background-color: transparent;
  color: white;
}
.btnClass {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  outline: none;
  // width:100%;
  height: 100%;
  background-color: transparent;
  border: none;
  color: #ddd;
  border-radius: 0px;
  font-size: 12px;
  max-width: 74px;
  position: relative;
  padding: 4px 12px;
  i {
    font-size: 22px;
  }
  &__active {
    color: #68d56a !important;
  }
  &__contentUpdated {
    &::after {
      position: absolute;
      right: 12px;
      top: 12px;
      content: "";
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: #f93032;
      animation: twinkling 1s infinite ease-in-out alternate;
    }
  }

  @keyframes twinkling {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
}

@media all and (max-width: 768px) {
  .toolBar {
    height: 42px;
    border-radius: 5px;
  }
  .btnClass {
    span {
      display: none;
    }
  }
}
</style>
